import React from 'react';
import { makeStyles } from '@mui/styles';
import Container from '@mui/material/Container';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableRow from '@mui/material/TableRow';
import TableHead from '@mui/material/TableHead';
import { CopyBlock, a11yDark } from "react-code-blocks";

const useStyles = makeStyles((theme) => ({
    wrapper: {
        overflowY: 'scroll',
        width: '100%',
    },
    table: {
        width: '90%',
        margin: '0 auto',
    },
    tableHead: {
        background: '#eee',
        position: 'sticky',
        top: 0,
    },
    copyBlock: {
        position: 'sticky',
        top: 0,
    },
}));


const sample = {
    year: 2022,
    month: 10,
    day: 14,
    week: "",
    hour: 9,
    minutes: 27,
    seconds: 3,
    timestamp: 1634172966,
    name: "",
    type: "SigFox",
    location: {
        name: "",
        latitude: 33.55082284753141,
        longitude: 130.3666567275178,
        direction: "",
        tilt: 30,
    },
    solar: {
        voltage: 5,
        current: 20,
        power: 0.4,
    },
    battery: 70,
    temperature: 20,
    pressure: 10,
    humidity: 30,
    gas: 0,
    co2: 0,
    uv: 0,
};

const ApiDocuments = () => {
    const classes = useStyles();
    return (
        <div className={classes.wrapper}>
            <Container>
                <Box padding={3} />
                <Typography variant="h4">
                    API 
                </Typography>
                <Box padding={3} />
                <Typography variant="h5">
                    URL
                </Typography>
                <Box padding={1} />
                <CopyBlock
                    text="https://tec-log2.azurewebsites.net/api/getSolarAll?code=oNgnwUM00BM2KCAt2LDb/yccjS9GNs87MXFuk5aRrIPvSPRZpQ3ViQ=="
                    language='plane'
                    wrapLines
                    theme={a11yDark}
                />
                <Box padding={3} />
                <Typography variant="h5">
                    
                </Typography>
                <Box padding={1} />
                <Grid container>
                    <Grid item xs={12} md={4}>
                        <div className={classes.copyBlock}>
                            <CopyBlock
                                text={JSON.stringify(sample, null, 6)}
                                language='javascript'
                                wrapLines
                                theme={a11yDark}
                            />
                        </div>
                    </Grid>
                    <Grid item xs={12} md={8}>
                        <Table className={classes.table} aria-label="simple table">
                            <TableHead className={classes.tableHead}>
                                <TableRow>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                <TableRow>
                                    <TableCell>year</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>month</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>day</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>week</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>hour</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>minutes</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>seconds</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>timestamp</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>[ms]</TableCell>
                                    <TableCell>197011(UTC)</TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>name</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>
                                        ""<br />
                                        "CO2"<br />
                                        ""
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>type</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>
                                        "IM920"<br />
                                        "WiFi"<br />
                                        "SigFox"
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>location.name</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>location.latitude</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>[ ]</TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>location.longitude</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>[ ]</TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>location.direction</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>location.tilt</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>[ ]</TableCell>
                                    <TableCell>
                                         0<br />
                                         90
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>solar.voltage</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>[V]</TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>solar.current</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>[mA]</TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>solar.power</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>[W]</TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>battery</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>[%]</TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>temperature</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>[]</TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>pressure</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>[hPa]</TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>humidity</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>[%]</TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>gas</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>[m]</TableCell>
                                    <TableCell></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>co2</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>[ppm]</TableCell>
                                    <TableCell>"CO2"<br /></TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell>uv</TableCell>
                                    <TableCell></TableCell>
                                    <TableCell>[mW/cm<sup>2</sup>]</TableCell>
                                    <TableCell>"CO2"<br /></TableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                    </Grid>
                </Grid>
                <Box padding={3} />
            </Container>
        </div>
    );
}
export default ApiDocuments