import React, { useState } from 'react';
import { useDispatch } from 'react-redux'
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import TextField from '@mui/material/TextField';
import DialogContentText from '@mui/material/DialogContentText';

import actions from '../../../actions';

//
const hankaku2Zenkaku = (str) => {
  return str.replace(/[---]/g, function (s) {
    return String.fromCharCode(s.charCodeAt(0) - 0xFEE0);
  });
}

const ChangeNodeNumber = () => {
  const dispatch = useDispatch();
  const [open, setOpen] = useState(false);
  const [errorFlag, setErrorFlag] = useState(false);
  const [emptyFlag, setEmptyFlag] = useState(true);
  const [errorMessage, setErrorMessage] = useState("");
  const [nodeNumber, setNodeNumber] = useState("00");
  const [text, setText] = useState("");

  const handleAgree = () => {
    setOpen(false);
    dispatch(actions.im920.wired.executeCommands(
      "nodeNumber",
      ["STNN " + nodeNumber + "\r", "RDNN\r"],
    ));
  };

  const handleChange = (event) => {
    let text = hankaku2Zenkaku(event.target.value);
    let head = text.slice(0, 2);
    if (head === "0x" || head === "0X") {
      text = text.slice(2); //
    }
    else {
      head = "";
    }
    if (text.length > 2) {
      setErrorMessage("");
      return;
    }
    setText(event.target.value);
    const number = parseInt(text, 16);
    let errorFlag = false;
    for (let i = 0; i < text.length; i++) {
      const n = parseInt(text.charAt(i), 16);
      if (Number.isNaN(n)) {
        errorFlag = true;
      }
    }
    if (text.length === 0) {
      setEmptyFlag(true);
      setErrorFlag(false);
      setErrorMessage("");
    }
    else if (number < 0) {
      setErrorFlag(true);
      setErrorMessage("");
    }
    else if (errorFlag) {
      setErrorFlag(true);
      setErrorMessage("162");
    }
    else {
      setEmptyFlag(false);
      setErrorFlag(false);
      setErrorMessage("");
      const nodeNumber = ('00' + number.toString(16).toUpperCase()).substr(-2);
      setNodeNumber(nodeNumber);
    }
  };

  return (
    <div>
      <Button variant="contained" size="small" color="primary" onClick={() => setOpen(true)}>
        
      </Button>
      <Dialog
        open={open}
        onClose={() => setOpen(false)}
      >
        <DialogTitle></DialogTitle>
        <DialogContent>
          <DialogContentText>
             ID  8bit <br />
            
          </DialogContentText>
          <TextField
            value={text}
            onChange={handleChange}
            error={errorFlag}
            helperText={errorMessage}
            label=""
            variant="outlined"
            autoFocus
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setOpen(false)} variant="contained">
            
          </Button>
          <Button
            disabled={errorFlag || emptyFlag}
            onClick={handleAgree}
            variant="contained"
            color="primary"
          >
            
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
export default ChangeNodeNumber;
